@import url(https://fonts.googleapis.com/css?family=Poppins:100,400&display=swap);
body{
    font-family: 'Poppins', sans-serif;;
}
.container{
    width:80%;
    margin: auto;
}
.card img{
    width:100% !important;
}
.card-flex{
    display: flex;
    flex-direction: column;
}
.card{
    
    -webkit-transition: .3s;
    
    transition: .3s;
}
.card-hover{
    width: 80%;
    cursor:pointer;
}
.card-hover:hover{
    -webkit-transform: translateY(-2%);
            transform: translateY(-2%);
    box-shadow: 10px 10px 80px rgb(71, 71, 71);
}
.book-container{
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-gap: 1rem;
}
.book-image img{
    border:1px solid rgb(158, 158, 158);
}
.book-description{
    border-radius: 5px;
    background:rgb(213, 252, 213);
    color:black;
    padding:10px 15px;
    border:1px solid rgb(212, 212, 212);
}
.book-details{
    margin-top: 10px;
    border: 1px solid rgb(214, 214, 214);
    padding:15px;
}
.book-details i{
    color:orange;
}
.flex-navbar{
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.form-inline{
  width: 50% !important;
  display: flex;
}
.form-inline input{
  width: 80% !important;
}
.form-inline button{
  width: 15%;
}
@media(max-width:1000px){
  .flex-navbar{
    flex-direction: column;
  }
  .form-inline{
    width:100% !important;
    display: flex;
    flex-direction: column;
  }
  .form-inline button{
    margin-top: .5rem !important;
    width: 100%;
  }
  .form-inline input{
    width: 100% !important;
  }
}
.bg-none{
  background: none !important;
  outline: none;
  border: none;
  color:green;
}

